.api-key-container {
  padding: 10px 16px;
  background-color: #f1f3f4;
  border-bottom: 1px solid #e0e0e0;
}

.key-input-group {
  display: flex;
  gap: 8px;
}

.input-key {
  flex: 1;
  padding: 6px 8px;
  border: 1px solid #ccc;
  border-radius: 60px;
  font-size: 10px;
}

.btn-save {
  background: #264aff;
  background: linear-gradient(90deg,rgba(38, 74, 255, 1) 0%, rgba(255, 46, 88, 1) 100%);
  color: white;
  width: 100%;
  border: 1px solid #dadce0;
  border-radius: 60px;
  box-sizing: border-box;
  font-size: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700;
  cursor: pointer;
}

.btn-save:hover {
  filter: brightness(80%);
}

.key-status {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
  color: #137333;
  font-weight: 500;
}

.btn-text {
  background: none;
  border: none;
  color: #1a73e8;
  cursor: pointer;
  text-decoration: underline;
  font-size: 12px;
}